ackground-color:#003d7a}}", "",{"version":3,"sources":["webpack://./resources/js/Components/Global/Popups/AddWebAppPopupNotification.vue"],"names":[],"mappings":"AAkHA,sBACE,6BACF,CAEA,wBAOE,kBAAmB,CADnB,YAAa,CAEb,sBAAuB,CAEvB,YAAa,CATb,cAAe,CAQf,WAEF,CAEA,wDARE,WAAY,CAFZ,MAAO,CADP,KAAM,CAEN,UAkBF,CATA,gCAOE,kCAA2B,CAA3B,0BAA2B,CAD3B,gBAA8B,CAL9B,iBAAkB,CAOlB,UACF,CAEA,gCAEE,wBAA4B,CAC5B,kBAAmB,CACnB,+BAAyC,CAKzC,eAAgB,CAFhB,eAAgB,CAGhB,eAAgB,CAThB,YAAa,CAUb,iBAAkB,CANlB,eAAgB,CAGhB,UAIF,CAEA,+BAME,UAAY,CADZ,cAAe,CADf,cAAe,CAHf,iBAAkB,CAElB,UAAW,CADX,QAAS,CAKT,oBACF,CAEA,qCACE,aACF,CAEA,2BAGE,aAAc,CADd,eAAgB,CADhB,YAGF,CAEA,0BAEE,aAAc,CADd,aAEF,CAEA,2BACE,iBACF,CAEA,8BAEE,aAAc,CADd,kBAEF,CAEA,oCAGE,aAAc,CADd,WAAY,CAEZ,aAAc,CAHd,WAIF,CAEA,4BAIE,wBAAyB,CAEzB,WAAY,CACZ,iBAAkB,CAFlB,UAAc,CAGd,cAAe,CAPf,oBAAqB,CAQrB,aAAc,CAPd,eAAgB,CAChB,iBAAkB,CAOlB,+BACF,CAEA,kCACE,wBACF,CAGA,+BACE,oCACF,CAEA,qCACE,oBACF,CAGA,uBACE,YAAa,CACb,QAAS,CACT,0BACF,CAGA,mCACE,gCACE,qBAAyB,CACzB,UACF,CAOA,mFACE,UACF,CAGA,+BACE,UACF,CAEA,qCACE,UACF,CAEA,4BACE,wBACF,CAEA,kCACE,wBACF,CACF","sourcesContent":["<template>\n  <div v-if=\"showPopup\" class=\"popup\" @click.self=\"closePopup\">\n    <div class=\"popup-overlay\"></div>\n    <div class=\"popup-content border border-white scrollbar-custom dark:scrollbar-custom-dark\">\n      <span class=\"close\" @click=\"closePopup\">&times;</span>\n      <h2 class=\"flex flex-wrap justify-center items-center\">Add <img :src=\"logoSrc\" class=\"w-20 h-auto\"> to Your Home Screen</h2>\n      <p class=\"mt-2\">\n        Experience notTV like never before by adding it to your home screen. This allows you to quickly access our\n        platform, bypass censorship, and enjoy a seamless user experience.\n      </p>\n      <p class=\"mt-2\">\n        Follow these 3 simple steps to add notTV to your home screen:\n      </p>\n      <ol class=\"mt-2\">\n        <li>\n          <strong>1. Open your browser's menu</strong> <br>\n          <div class=\"flex gap-2 justify-start\">\n          <SingleImageWithModal\n              :placeholder=\"`https://cdn.nottv.io/public/2024/06/images/Xo6uaMUzEyE0YIfTCfun3yiGZvQ4mq056s1bgq7n.png`\"\n              :alt=\"`Apple Browser menu icon`\"\n              :class=\"`w-20 h-20 object-contain my-2 transition-transform duration-300 ease-in-out hover:scale-110`\"\n              :caption=\"`iOS`\"\n          />\n            <SingleImageWithModal\n                :placeholder=\"`https://cdn.nottv.io/public/2024/06/images/8f8UbaNuOwLHBICGoM1De2zUhYcv976ATPXwUpFI.png`\"\n                :alt=\"`Android Browser menu icon`\"\n                :class=\"`w-20 h-20 object-contain my-2 transition-transform duration-300 ease-in-out hover:scale-110`\"\n                :caption=\"`Android`\"\n            />\n          </div>\n        </li>\n        <li>\n          <strong>2. Tap \"Add to Home Screen\"</strong> <br>\n          <div class=\"flex gap-2 justify-start\">\n          <SingleImageWithModal\n              :placeholder=\"`https://cdn.nottv.io/public/2024/06/images/bnxKWcskRQvS7sQEBKPeTXCaLx4QY48eko0ieOCr.png`\"\n              :alt=\"`Apple Add to Home Screen icon`\"\n              :class=\"`w-20 h-20 object-contain my-2 transition-transform duration-300 ease-in-out hover:scale-110`\"\n              :caption=\"`iOS`\"\n          />\n            <SingleImageWithModal\n                :placeholder=\"`https://cdn.nottv.io/public/2024/06/images/NlnU6ypPl2VFg9lD2FPU82SKrZZgnNVP3sXsD08E.png`\"\n                :alt=\"`Android Add to Home Screen icon`\"\n                :class=\"`w-20 h-20 object-contain my-2 transition-transform duration-300 ease-in-out hover:scale-110`\"\n                :caption=\"`Android`\"\n            />\n          </div>\n        </li>\n        <li>\n          <strong>3. Follow the prompts to add notTV</strong> <br>\n          <div class=\"flex gap-2 justify-start\">\n            <SingleImageWithModal\n                :placeholder=\"`https://cdn.nottv.io/public/2024/06/images/I9OAS7KYX3ohdsS7VbCJaBW0t1juJXzgBRSCRwfa.png`\"\n                :alt=\"`Follow prompts icon`\"\n                :class=\"`w-20 h-20 object-contain my-2 transition-transform duration-300 ease-in-out hover:scale-110`\"\n            />\n            <SingleImageWithModal\n                :placeholder=\"`https://cdn.nottv.io/public/2024/06/images/KCZJzt1QiMYCajjLoVIMgAWAeMAh18VSP0hYfget.png`\"\n                :alt=\"`Home Screen icon`\"\n                :class=\"`w-20 h-20 object-contain my-2 transition-transform duration-300 ease-in-out hover:scale-110`\"\n            />\n          </div>\n        </li>\n      </ol>\n      <p>\n        By adding notTV to your home screen, you ensure you have direct access to our content, free from restrictions\n        and enhanced for your convenience.\n      </p>\n      <button @click=\"closePopup\" class=\"btn-close\">Got it!</button>\n    </div>\n  </div>\n</template>\n\n<script setup>\nimport SingleImageWithModal from '@/Components/Global/Multimedia/SingleImageWithModal.vue'\nimport { computed, onMounted, onUnmounted } from 'vue'\n\nconst props = defineProps({\n  showPopup: {\n    type: Boolean,\n    required: true,\n  },\n})\n\nconst emit = defineEmits(['close'])\n\nconst closePopup = () => {\n  emit('close')\n}\n\nconst logoSrc = computed(() => {\n  return window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches\n      ? '/storage/images/logo_white.svg'\n      : '/storage/images/logo_black_311.png';\n});\n\nconst handleKeyDown = (event) => {\n  if (event.key === 'Escape') {\n    closePopup();\n  }\n};\n\nonMounted(() => {\n  document.addEventListener('keydown', handleKeyDown);\n});\n\nonUnmounted(() => {\n  document.removeEventListener('keydown', handleKeyDown);\n});\n</script>\n\n<style scoped>\n@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');\n\nbody {\n  font-family: 'Roboto', sans-serif;\n}\n\n.popup {\n  position: fixed;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 998;\n  padding: 20px;\n}\n\n.popup-overlay {\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.8);\n  backdrop-filter: blur(10px);\n  z-index: -1;\n}\n\n.popup-content {\n  padding: 30px;\n  background-color: whitesmoke;\n  border-radius: 10px;\n  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.3);\n  text-align: left;\n  color: ;\n  max-width: 500px;\n  width: 100%;\n  max-height: 80vh; /* Ensure the popup height does not exceed the viewport height */\n  overflow-y: auto; /* Make content scrollable if it exceeds the popup height */\n  position: relative;\n}\n\n.popup .close {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  font-size: 25px;\n  cursor: pointer;\n  color: black;\n  transition: color 0.2s;\n}\n\n.popup .close:hover {\n  color: #ea4335;\n}\n\n.popup h2 {\n  margin-top: 0;\n  font-size: 1.8em;\n  color: #1a73e8;\n}\n\n.popup p {\n  font-size: 1em;\n  color: #202124;\n}\n\n.popup ol {\n  padding-left: 20px;\n}\n\n.popup ol li {\n  margin-bottom: 15px;\n  color: #202124;\n}\n\n.instruction-image {\n  width: 100px; /* Adjust size as needed */\n  height: auto;\n  display: block;\n  margin: 10px 0;\n}\n\n.btn-close {\n  display: inline-block;\n  margin-top: 20px;\n  padding: 10px 20px;\n  background-color: #34a853;\n  color: #ffffff;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  font-size: 1em;\n  transition: background-color 0.3s;\n}\n\n.btn-close:hover {\n  background-color: #2a8535;\n}\n\n/* Hover effect for SingleImageWithModal */\n.single-image {\n  transition: transform 0.3s ease-in-out;\n}\n\n.single-image:hover {\n  transform: scale(1.1);\n}\n\n/* Flexbox to align images side by side */\n.flex {\n  display: flex;\n  gap: 10px;\n  justify-content: flex-start;\n}\n\n/* Dark mode styling */\n@media (prefers-color-scheme: dark) {\n  .popup-content {\n    background-color: #333333;\n    color: #ffffff;\n  }\n\n  .popup h2,\n  .popup p {\n    color: #ffffff;\n  }\n\n  .popup ol li {\n    color: #ffffff;\n  }\n\n\n  .popup .close {\n    color: #bbbbbb;\n  }\n\n  .popup .close:hover {\n    color: #ffffff;\n  }\n\n  .btn-close {\n    background-color: #0056b3;\n  }\n\n  .btn-close:hover {\n    background-color: #003d7a;\n  }\n}\n</style>\n"],"sourceRoot":""}]);
