ust breakpoint as needed */\n  .ottButton {\n    order: 3; /* Make other buttons take precedence in the first row */\n  }\n\n  #first-btn {\n    order: 0; /* Ensure the first button moves to the start of the second row when wrapped */\n  }\n}\n</style>\n"],"sourceRoot":""}]);
